#computingOverlay {
  width: 100%;
  height: 100%;
  z-index: 2;
  cursor: wait;
  position: fixed;
  inset: 0;
}

.ss-solver-layout {
  grid-template: ". header header ."
                 ". puzzle puzzle ."
                 ". actions select ."
                 ". infos infos ."
                 / 50px 200px auto 50px;
  gap: 10px;
  display: grid;
}

.ss-layout-heading {
  text-align: center;
  grid-area: header;
}

.ss-select-sudoku {
  grid-area: select;
}

.ss-select-sudoku div {
  display: flexbox;
}

.ss-actions-container {
  flex-direction: column;
  grid-area: actions;
  display: flex;
}

.ss-actions-container p {
  margin: 6px 0 2px;
}

.ss-infos-container {
  grid-area: infos;
  justify-content: center;
  display: flex;
}

.ss-option-open {
  height: 18px;
  width: 18px;
  color: #000;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background: #a488c9;
  font-size: 16px;
}

.ss-option-closed {
  height: 18px;
  width: 18px;
  color: #000;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background: #ab95c7;
  font-size: 16px;
}

.ss-forced-cell {
  height: 54px;
  width: 54px;
  color: #000;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background: #a488c9;
  border: 1px solid #000;
  font-size: 48px;
  overflow: hidden;
}

.ss-cell-container {
  border: 1px solid #000;
  grid-template-rows: repeat(3, auto);
  grid-template-columns: repeat(3, auto);
  grid-auto-flow: row;
  display: inline-grid;
}

.ss-sudoku-border {
  background: #4120a9;
}

.ss-puzzle-container {
  grid-area: puzzle;
  place-self: center;
  align-items: flex-start;
  display: flex;
}

.ss-metaboard-info-readout {
  align-self: flex-start;
  margin-left: 15px;
}

.ss-metaboard-info-readout h4 {
  margin-top: 0;
}

.ss-metaboard-info-readout p {
  overflow-wrap: break-word;
  white-space: pre-wrap;
  font-family: monospace;
}

.ss-board-container {
  flex-direction: column;
  display: flex;
}

.ss-board-container h4 {
  text-align: center;
  margin-top: 0;
}

.ss-sudoku-board {
  width: min-content;
  height: min-content;
  border: 15px solid indigo;
  border-radius: 30px;
  grid-template-rows: repeat(3, auto) 5px repeat(3, auto) 5px repeat(3, auto);
  grid-template-columns: repeat(3, auto) 5px repeat(3, auto) 5px repeat(3, auto);
  grid-auto-flow: row;
  display: inline-grid;
}

